.controller#container{
    display: flex;
    flex-direction: column;
    background-color: var(--van-gray-8);
    border-radius: 12px;
}
.controller#asset{
    font-size: x-large;
    font-weight: 800;
    text-align: center;
}

.controller#element{
    font-size: larger;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 10px;
}
.controller#comment{
    width: 90%;
    text-align: left;
}
input{
    width: 90%;
    accent-color:green;
}
.controller#able{
    font-size: x-large;
    font-weight: 800;
    text-align: center;
    margin-top: 5px;
    padding: 10px;
    border-radius: 12px;
    background-color: green;
}
.controller#disable{
    font-size: x-large;
    font-weight: 800;
    text-align: center;
    margin-top: 5px;
    padding: 10px;
    border-radius: 12px;
    background-color: gray;
}