body.auth{
    display: flex;
    justify-content: center;
    align-items: center;
}
.auth#container{
    display: flex;
    flex-direction: column;
    background-color: var(--van-gray-8);
    border-radius: 12px;
    padding: 20px;
}
.auth{
    margin:10px;
}
input.auth{
    font-size: 20px;
    padding:12px;
}
button.auth{
    font-size: 15px;
    padding:10px;
}
.auth#signup{
    color:var(--text-violet);
    margin: 0;
    margin-left: 20px;
    font-weight: 600;
}
.auth#comment{
    color: red;
    font-weight: 800;
}