@font-face {
  font-family: "notosans";
  font-weight: normal;
  src: url("./Component/Static/font/NotoSansKR-VariableFont_wght.ttf") format("truetype");
}


body{
  font-family: "notosans";
  background-color: black;
  color: rgba(255, 255, 255, 0.87);
}
button{
  font-family: "notosans";
  background-color: #1d72db;
  border:none;
  color: white;
  border-radius: 5px;
}
input{
  background-color: var(--van-gray-6);
  outline: none;
  border-radius: 5px;
  border:none;
  padding:5px;
}
input:focus{
  background-color: var(--van-gray-5);
}
textarea{
  font-family: "notosans";
}