.readymodal {
    background-color: var(--van-gray-8);
    position: absolute;
    top: 30%;
    left: 50%;
    transform: translate(-50%, -50%);
    /* 추가 스타일 */
    padding: 20px;
    border-radius: 10px;
    text-align: center;
    opacity: 0.95;
    z-index: 0;
}
div#count{
    margin-top: 15px;
    font-size: xx-large;
    font-weight: 800;
    margin-bottom: 15px;
}
div#comment{
    font-weight: 800;
}