.chatting#input{
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    bottom: 0px;
    padding: 10px;
}
.chatting#container{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
}
.chatting#textcontainer{
    display: flex;
    justify-content: center;
    width: 85%;
    padding: 10px;
    border-radius: 12px;
    background-color: var(--van-gray-8);
}
textarea.chatting{
    width: 100%;
    padding: 0px;
    outline: none;
    border: none;
    background-color: var(--van-gray-8);
    font-size:medium;
    color: white;
    resize: none;
    overflow: auto;
}
button.chatting{
    width: 80px;
    font-size: 3vw;
}
.chatlog#container{
    flex-grow: 1;
    overflow-y: auto;
}
div.chatlog#title{
    width: 100%;
    position: absolute;
    background-color: black;
}
div.element#container{
    margin: 10px;
    display: flex;
    flex-direction: column;
    /*align-items: flex-end;*/
}
.element#nickname{
    font-size: small;
    font-weight: 800;
    margin-bottom: 3px;
    margin-top: 8px;
    margin-left: 8px;
    margin-right: 8px;
}
.element#content{
    font-size: x-small;
    width: fit-content;
    max-width: 80%;                     /* 최대 너비를 부모의 80%로 설정 */
    border-radius: 8px;                 /* 모서리를 둥글게 설정 */
    padding: 10px;                      /* 내부 여백 설정 */
    background-color: var(--van-gray-8); /* 배경 색상 설정 */
    color: white;                       /* 텍스트 색상 (예시로 흰색 추가) */
    text-align: left;                   /* 텍스트 왼쪽 정렬 */
    word-wrap: break-word;              /* 긴 단어가 부모 요소의 경계를 넘어갈 경우 줄 바꿈 */
    overflow-wrap: break-word;          /* 더 나은 단어 줄 바꿈 처리 */
    white-space: normal;                /* 기본 줄 바꿈 설정 */
}
